@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("Montserrat Regular"), local("Montserrat-Regular"), url("../fonts/montserrat.woff2") format("woff2"), url("../fonts/montserrat.woff") format("woff"); }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: local("Montserrat SemiBold"), local("Montserrat-SemiBold"), url("../fonts/montserrat-600.woff2") format("woff2"), url("../fonts/montserrat-600.woff") format("woff"); }

@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("Montserrat Bold"), local("Montserrat-Bold"), url("../fonts/montserrat-bold.woff2") format("woff2"), url("../fonts/montserrat-bold.woff") format("woff"); }

body {
  min-width: 320px;
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 18px;
  font-family: "Montserrat", "Arial", sans-serif;
  font-style: normal;
  color: #011c40;
  background-color: #f9fbfd; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.main__wrapper {
  margin: 0 auto;
  padding: 35px 0;
  max-width: 1000px;
  position: relative; }
  .main__wrapper::before {
    content: "";
    position: absolute;
    left: calc(50% - 1px);
    top: 35px;
    width: 2px;
    height: calc(100% - 70px);
    background-color: #ffd966; }

.main__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr;
  grid-template-columns: 1fr 1fr; }

.main__item {
  margin-top: 30px;
  padding-left: 110px;
  width: 100%;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  position: relative; }

.main__item:nth-child(1) {
  -ms-grid-row: 1;
  grid-row-start: 1;
  -ms-grid-row-span: 2;
  grid-row-end: 3; }

.main__item:nth-child(2) {
  -ms-grid-row: 2;
  grid-row-start: 2;
  -ms-grid-row-span: 2;
  grid-row-end: 4; }

.main__item:nth-child(3) {
  -ms-grid-row: 3;
  grid-row-start: 3;
  -ms-grid-row-span: 2;
  grid-row-end: 5; }

.main__item:nth-child(4) {
  -ms-grid-row: 4;
  grid-row-start: 4;
  -ms-grid-row-span: 2;
  grid-row-end: 6; }

.main__item:nth-child(2n+1)::before {
  content: "";
  position: absolute;
  right: -1px;
  top: 37px;
  width: 120px;
  border-bottom: 2px dashed #ffd966; }

.main__item:nth-child(2n+1)::after {
  content: "";
  position: absolute;
  right: -12px;
  top: 25px;
  width: 24px;
  height: 24px;
  border-radius: 50%;
  background-color: #ffd966; }

.main__item:nth-child(2n) {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end; }
  .main__item:nth-child(2n)::before {
    content: "";
    position: absolute;
    left: 0;
    top: 47px;
    width: 70px;
    border-bottom: 2px dashed #ffd966; }
  .main__item:nth-child(2n)::after {
    content: "";
    position: absolute;
    left: -12px;
    top: 35px;
    width: 24px;
    height: 24px;
    border-radius: 50%;
    background-color: #ffd966; }

.main__item-badge::before {
  content: "";
  position: absolute;
  left: -110px;
  top: 0;
  width: 104px;
  height: 98px;
  background-image: url("../img/badge.png");
  background-size: 104px 98px; }

.main__item-phone::before {
  content: "";
  position: absolute;
  left: -110px;
  top: 0;
  width: 103px;
  height: 94px;
  background-image: url("../img/phone.png");
  background-size: 103px 94px; }

.main__item-result::before {
  content: "";
  position: absolute;
  left: -110px;
  top: 0;
  width: 100px;
  height: 101px;
  background-image: url("../img/result.png");
  background-size: 100px 101px; }

.main__item-wrapper {
  width: 305px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  position: relative; }

h2 {
  margin: 0;
  margin-bottom: 12px;
  font-weight: bold;
  font-size: 72px;
  color: transparent;
  text-transform: uppercase;
  -webkit-text-stroke: 2px #676767;
  text-shadow: -8px -8px #ffd966; }

h3 {
  margin: 0;
  margin-bottom: 12px;
  font-weight: bold;
  font-size: 24px;
  color: #011c40; }

p {
  margin: 0;
  margin-bottom: 20px;
  font-size: 18px;
  color: #011c40; }

.main__item-link {
  font-size: 18px;
  font-weight: 600;
  color: #6accb2;
  cursor: pointer;
  outline: none; }

.main__item-link:hover,
.main__item-link:focus {
  opacity: 0.7; }

.main__item-link:active {
  opacity: 0.5; }

@media (max-width: 999px) {
  .main__item {
    margin-top: 15px;
    padding-left: 100px;
    padding-right: 40px; }
  .main__item-wrapper {
    width: 100%; }
  .main__item-badge::before {
    left: -85px;
    width: 76px;
    height: 71px;
    background-size: 76px 71px; }
  .main__item-phone::before {
    left: -85px;
    width: 75px;
    height: 68px;
    background-size: 75px 68px; }
  .main__item-result::before {
    left: -85px;
    width: 72px;
    height: 74px;
    background-size: 72px 74px; }
  h2 {
    margin-bottom: 12px;
    font-size: 52px; }
  h3 {
    margin-bottom: 12px;
    font-size: 18px; }
  p {
    margin-bottom: 20px;
    font-size: 12px; }
  .main__item-link {
    font-size: 13px; }
  .main__item:nth-child(2n+1)::before {
    top: 24px;
    width: 25%; }
  .main__item:nth-child(2n+1)::after {
    right: -9px;
    top: 15px;
    width: 18px;
    height: 18px; }
  .main__item:nth-child(2n) {
    padding-left: 120px;
    padding-right: 10px; }
    .main__item:nth-child(2n)::before {
      top: 34px;
      width: 7%; }
    .main__item:nth-child(2n)::after {
      left: -9px;
      top: 25px;
      width: 18px;
      height: 18px; } }

@media (max-width: 767px) {
  .main__wrapper::before {
    content: none; }
  .main__list {
    display: block; }
  .main__item {
    margin-bottom: 60px;
    padding-left: 75px;
    padding-right: 10px;
    width: 100%; }
  .main__item:nth-child(2n+1)::before {
    content: none; }
  .main__item:nth-child(2n+1)::after {
    content: none; }
  .main__item:nth-child(2n) {
    padding-left: 75px;
    padding-right: 10px; }
    .main__item:nth-child(2n)::before {
      content: none; }
    .main__item:nth-child(2n)::after {
      content: none; }
  .main__item:last-of-type {
    margin-bottom: 0; } }
